* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	user-select: none;
}

body {
	font-size: 16px;
	min-width: 320px;
	font-family: Arial, sans-serif;
	width: 100%;
	min-height: 100vh;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: inherit;
	font-weight: 400;
}

ul {
	list-style: none;
}

ul,
ol {
	padding: 0;
}

a {
	text-decoration: none;
	color: inherit;
	display: inline-block;
}

input,
textarea,
select {
	outline: none;
	border: 1px solid gray;
	appearance: none;
	background-color: transparent;
	font: inherit;
	color: inherit;
}

button {
	padding: 0;
	border: none;
	font: inherit;
	color: inherit;
	background-color: transparent;
}

abbr {
	text-decoration: none;
}

img,
fieldset {
	border: 0;
}

img {
	max-width: 100%;
}